html {
  font-family: 'Roboto', ProximaNova, -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  font-size: 15px;
  font-weight: normal;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
  background: #fff;
  color: #666;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  margin: 0;
}

a {
  -webkit-text-decoration-skip: objects;
}

a:active,
a:hover {
  outline: none;
}

a,
.uk-link {
  color: #1e87f0;
  text-decoration: none;
  cursor: pointer;
}

a:hover,
.uk-link:hover {
  color: #0f6ecd;
  text-decoration: underline;
}

abbr[title] {
  text-decoration: underline;
  -webkit-text-decoration-style: dotted;
}

b,
strong {
  font-weight: bolder;
}

:not(pre) > code,
:not(pre) > kbd,
:not(pre) > samp {
  font-family: 'Roboto Mono', monospace;
  font-size: 12px;
  color: #f0506e;
  white-space: nowrap;
  padding: 2px 6px;
  background: #f8f8f8;
}

em {
  color: #f0506e;
}

ins {
  background: #ffd;
  color: #666;
  text-decoration: none;
}

mark {
  background: #ffd;
  color: #666;
}

q {
  font-style: italic;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

audio,
canvas,
img,
video {
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
}

@supports (display: block) {
  svg {
    max-width: 100%;
    height: auto;
    box-sizing: border-box;
  }
}

svg:not(:root) {
  overflow: hidden;
}

img:not([src]) {
  visibility: hidden;
}

iframe {
  border: 0;
}

p,
ul,
ol,
dl,
pre,
address,
fieldset,
figure {
  margin: 0 0 20px 0;
}

* + p,
* + ul,
* + ol,
* + dl,
* + pre,
* + address,
* + fieldset,
* + figure {
  margin-top: 20px;
}

h1,
.uk-h1,
h2,
.uk-h2,
h3,
.uk-h3,
h4,
.uk-h4,
h5,
.uk-h5,
h6,
.uk-h6 {
  margin: 0 0 20px 0;
  font-family: ProximaNova, -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, 'Helvetica Neue', Arial, sans-serif;
  font-weight: 300;
  color: #222;
  text-transform: none;
}

* + h1,
* + .uk-h1,
* + h2,
* + .uk-h2,
* + h3,
* + .uk-h3,
* + h4,
* + .uk-h4,
* + h5,
* + .uk-h5,
* + h6,
* + .uk-h6 {
  margin-top: 40px;
}

h1,
.uk-h1 {
  font-size: 32.3px;
  line-height: 1.2;
}

h2,
.uk-h2 {
  font-size: 25.5px;
  line-height: 1.3;
}

h3,
.uk-h3 {
  font-size: 24px;
  line-height: 1.4;
}

h4,
.uk-h4 {
  font-size: 20px;
  line-height: 1.4;
}

h5,
.uk-h5 {
  font-size: 15px;
  line-height: 1.4;
}

h6,
.uk-h6 {
  font-size: 14px;
  line-height: 1.4;
}

@media (min-width: 960px) {
  h1,
  .uk-h1 {
    font-size: 38px;
  }

  h2,
  .uk-h2 {
    font-size: 30px;
  }
}

ul,
ol {
  padding-left: 30px;
}

ul > li > ul,
ul > li > ol,
ol > li > ol,
ol > li > ul {
  margin: 0;
}

dt {
  font-weight: bold;
}

dd {
  margin-left: 0;
}

hr,
.uk-hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  text-align: inherit;
  margin: 0 0 20px 0;
  border: 0;
  border-top: 1px solid #e5e5e5;
}

* + hr,
* + .uk-hr {
  margin-top: 20px;
}

address {
  font-style: normal;
}

blockquote {
  margin: 0 0 20px 0;
  font-size: 20px;
  line-height: 1.5;
  font-style: italic;
  color: #222;
}

* + blockquote {
  margin-top: 20px;
}

blockquote p:last-of-type {
  margin-bottom: 0;
}

blockquote footer {
  margin-top: 10px;
  font-size: 14px;
  line-height: 1.5;
  color: #666;
}

blockquote footer::before {
  content: 'â€” ';
}

pre {
  font: 12px / 1.5 'Roboto Mono', monospace;
  color: #666;
  -moz-tab-size: 4;
  tab-size: 4;
  overflow: auto;
  padding: 25px;
  border: 0 solid #e5e5e5;
  border-radius: 0;
  background: #f8f8f8;
}

pre code {
  font-family: 'Roboto Mono', monospace;
}

::selection {
  background: #39f;
  color: #fff;
  text-shadow: none;
}

details,
main {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

.uk-breakpoint-s::before {
  content: '640px';
}

.uk-breakpoint-m::before {
  content: '960px';
}

.uk-breakpoint-l::before {
  content: '1200px';
}

.uk-breakpoint-xl::before {
  content: '1600px';
}

:root {
  --uk-breakpoint-s: 640px;
  --uk-breakpoint-m: 960px;
  --uk-breakpoint-l: 1200px;
  --uk-breakpoint-xl: 1600px;
}

a.uk-link-muted,
.uk-link-muted a {
  color: #999;
}

a.uk-link-muted:hover,
.uk-link-muted a:hover {
  color: #666;
}

a.uk-link-text:not(:hover),
.uk-link-text a:not(:hover) {
  color: inherit;
}

a.uk-link-text:hover,
.uk-link-text a:hover {
  color: #999;
}

a.uk-link-heading:not(:hover),
.uk-link-heading a:not(:hover) {
  color: inherit;
}

a.uk-link-heading:hover,
.uk-link-heading a:hover {
  color: #1e87f0;
  text-decoration: none;
}

a.uk-link-reset,
a.uk-link-reset:hover,
.uk-link-reset a,
.uk-link-reset a:hover {
  color: inherit !important;
  text-decoration: none !important;
}

.uk-heading-primary {
  font-size: 3rem;
  line-height: 1.2;
}

@media (min-width: 960px) {
  .uk-heading-primary {
    font-size: 3.375rem;
  }
}

@media (min-width: 1200px) {
  .uk-heading-primary {
    font-size: 3.75rem;
    line-height: 1.1;
  }
}

.uk-heading-hero {
  font-size: 4rem;
  line-height: 1.1;
}

@media (min-width: 960px) {
  .uk-heading-hero {
    font-size: 6rem;
    line-height: 1;
  }
}

@media (min-width: 1200px) {
  .uk-heading-hero {
    font-size: 8rem;
    line-height: 1;
  }
}

.uk-heading-divider {
  padding-bottom: 10px;
  border-bottom: 1px solid #e5e5e5;
}

.uk-heading-bullet {
  position: relative;
}

.uk-heading-bullet::before {
  content: '';
  display: inline-block;
  position: relative;
  top: calc(-0.1 * 1em);
  vertical-align: middle;
  height: 0.9em;
  margin-right: 10px;
  border-left: 5px solid #e5e5e5;
}

.uk-heading-line {
  overflow: hidden;
}

.uk-heading-line > * {
  display: inline-block;
  position: relative;
}

.uk-heading-line > ::before,
.uk-heading-line > ::after {
  content: '';
  position: absolute;
  top: calc(50% - (1px / 2));
  width: 2000px;
  border-bottom: 1px solid #e5e5e5;
}

.uk-heading-line > ::before {
  right: 100%;
  margin-right: 0.6em;
}

.uk-heading-line > ::after {
  left: 100%;
  margin-left: 0.6em;
}

[class*='uk-divider'] {
  border: none;
  margin-bottom: 20px;
}

* + [class*='uk-divider'] {
  margin-top: 20px;
}

.uk-divider-icon {
  position: relative;
  height: 20px;
  background-image: url('data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20viewBox%3D%220%200%2020%2020%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Ccircle%20fill%3D%22none%22%20stroke%3D%22%23e5e5e5%22%20stroke-width%3D%222%22%20cx%3D%2210%22%20cy%3D%2210%22%20r%3D%227%22%20%2F%3E%0A%3C%2Fsvg%3E%0A');
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.uk-divider-icon::before,
.uk-divider-icon::after {
  content: '';
  position: absolute;
  top: 50%;
  max-width: calc(50% - (50px / 2));
  border-bottom: 1px solid #e5e5e5;
}

.uk-divider-icon::before {
  right: calc(50% + (50px / 2));
  width: 100%;
}

.uk-divider-icon::after {
  left: calc(50% + (50px / 2));
  width: 100%;
}

.uk-divider-small {
  line-height: 0;
}

.uk-divider-small::after {
  content: '';
  display: inline-block;
  width: 100px;
  max-width: 100%;
  border-top: 1px solid #e5e5e5;
  vertical-align: top;
}

.uk-divider-vertical {
  width: 1px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  border-left: 1px solid #e5e5e5;
}

.uk-list {
  padding: 0;
  list-style: none;
}

.uk-list > li::before,
.uk-list > li::after {
  content: '';
  display: table;
}

.uk-list > li::after {
  clear: both;
}

.uk-list > li > :last-child {
  margin-bottom: 0;
}

.uk-list ul {
  margin: 0;
  padding-left: 30px;
  list-style: none;
}

.uk-list > li:nth-child(n + 2),
.uk-list > li > ul {
  margin-top: 10px;
}

.uk-list-divider > li:nth-child(n + 2) {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid #e5e5e5;
}

.uk-list-striped > li {
  padding: 10px 10px;
}

.uk-list-striped > li:nth-of-type(odd) {
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
}

.uk-list-striped > li:nth-of-type(odd) {
  background: #f8f8f8;
}

.uk-list-striped > li:nth-child(n + 2) {
  margin-top: 0;
}

.uk-list-bullet > li {
  position: relative;
  padding-left: calc(1.5em + 10px);
}

.uk-list-bullet > li::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 1.5em;
  height: 1.5em;
  background-image: url('data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%226%22%20height%3D%226%22%20viewBox%3D%220%200%206%206%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%0A%20%20%20%20%3Ccircle%20fill%3D%22%23666%22%20cx%3D%223%22%20cy%3D%223%22%20r%3D%223%22%20%2F%3E%0A%3C%2Fsvg%3E');
  background-repeat: no-repeat;
  background-position: 50% 50%;
  display: block;
}

.uk-list-large > li:nth-child(n + 2),
.uk-list-large > li > ul {
  margin-top: 20px;
}

.uk-list-large.uk-list-divider > li:nth-child(n + 2) {
  margin-top: 20px;
  padding-top: 20px;
}

.uk-list-large.uk-list-striped > li {
  padding: 20px 10px;
}

.uk-list-large.uk-list-striped > li:nth-of-type(odd) {
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
}

.uk-list-large.uk-list-striped > li:nth-child(n + 2) {
  margin-top: 0;
}

.uk-description-list > dt {
  color: #222;
  font-size: 14px;
  font-weight: normal;
  text-transform: uppercase;
}

.uk-description-list > dt:nth-child(n + 2) {
  margin-top: 20px;
}

.uk-description-list-divider > dt:nth-child(n + 2) {
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #e5e5e5;
}

.uk-table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  margin-bottom: 20px;
}

* + .uk-table {
  margin-top: 20px;
}

.uk-table th {
  padding: 16px 12px;
  text-align: left;
  vertical-align: bottom;
  font-size: 12px;
  font-weight: normal;
  color: #999;
  text-transform: uppercase;
}

.uk-table td {
  padding: 16px 12px;
  vertical-align: top;
}

.uk-table td > :last-child {
  margin-bottom: 0;
}

.uk-table tfoot {
  font-size: 14px;
}

.uk-table caption {
  font-size: 14px;
  text-align: left;
  color: #999;
}

.uk-table-middle,
.uk-table-middle td {
  vertical-align: middle !important;
}

.uk-table-divider > tr:not(:first-child),
.uk-table-divider > :not(:first-child) > tr,
.uk-table-divider > :first-child > tr:not(:first-child) {
  border-top: 1px solid #e5e5e5;
}

.uk-table-striped > tr:nth-of-type(odd),
.uk-table-striped tbody tr:nth-of-type(odd) {
  background: #f8f8f8;
  border-top: 1px solid #e5e5e5;
  border-bottom: 1px solid #e5e5e5;
}

.uk-table-hover > tr:hover,
.uk-table-hover tbody tr:hover {
  background: #ffd;
}

.uk-table > tr.uk-active,
.uk-table tbody tr.uk-active {
  background: #ffd;
}

.uk-table-small th,
.uk-table-small td {
  padding: 10px 12px;
}

.uk-table-large th,
.uk-table-large td {
  padding: 22px 12px;
}

.uk-table-justify th:first-child,
.uk-table-justify td:first-child {
  padding-left: 0;
}

.uk-table-justify th:last-child,
.uk-table-justify td:last-child {
  padding-right: 0;
}

.uk-table-shrink {
  width: 1px;
}

.uk-table-expand {
  min-width: 150px;
}

.uk-table-link {
  padding: 0 !important;
}

.uk-table-link > a {
  display: block;
  padding: 16px 12px;
}

.uk-table-small .uk-table-link > a {
  padding: 10px 12px;
}

@media (max-width: 959px) {
  .uk-table-responsive,
  .uk-table-responsive tbody,
  .uk-table-responsive th,
  .uk-table-responsive td,
  .uk-table-responsive tr {
    display: block;
  }

  .uk-table-responsive thead {
    display: none;
  }

  .uk-table-responsive th,
  .uk-table-responsive td {
    width: auto !important;
    max-width: none !important;
    min-width: 0 !important;
    overflow: visible !important;
    white-space: normal !important;
  }

  .uk-table-responsive th:not(:first-child):not(.uk-table-link),
  .uk-table-responsive td:not(:first-child):not(.uk-table-link),
  .uk-table-responsive .uk-table-link:not(:first-child) > a {
    padding-top: 5px !important;
  }

  .uk-table-responsive th:not(:last-child):not(.uk-table-link),
  .uk-table-responsive td:not(:last-child):not(.uk-table-link),
  .uk-table-responsive .uk-table-link:not(:last-child) > a {
    padding-bottom: 5px !important;
  }

  .uk-table-justify.uk-table-responsive th,
  .uk-table-justify.uk-table-responsive td {
    padding-left: 0;
    padding-right: 0;
  }
}

.uk-table tbody tr {
  transition: background-color 0.1s linear;
}

.uk-icon {
  margin: 0;
  border: none;
  border-radius: 0;
  overflow: visible;
  font: inherit;
  color: inherit;
  text-transform: none;
  padding: 0;
  background-color: transparent;
  display: inline-block;
  fill: currentcolor;
  line-height: 0;
}

button.uk-icon:not(:disabled) {
  cursor: pointer;
}

.uk-icon::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.uk-icon:not(.uk-preserve) [fill*='#']:not(.uk-preserve) {
  fill: currentcolor;
}

.uk-icon:not(.uk-preserve) [stroke*='#']:not(.uk-preserve) {
  stroke: currentcolor;
}

.uk-icon > * {
  transform: translate(0, 0);
}

.uk-icon-image {
  width: 20px;
  height: 20px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: middle;
}

.uk-icon-link {
  color: #999;
}

.uk-icon-link:hover,
.uk-icon-link:focus {
  color: #666;
  outline: none;
}

.uk-icon-link:active,
.uk-active > .uk-icon-link {
  color: #595959;
}

.uk-icon-button {
  box-sizing: border-box;
  width: 36px;
  height: 36px;
  border-radius: 500px;
  background: #f8f8f8;
  color: #999;
  vertical-align: middle;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: 0.1s ease-in-out;
  transition-property: color, background-color;
}

.uk-icon-button:hover,
.uk-icon-button:focus {
  background-color: #ebebeb;
  color: #666;
  outline: none;
}

.uk-icon-button:active,
.uk-active > .uk-icon-button {
  background-color: #dfdfdf;
  color: #666;
}

.uk-range {
  box-sizing: border-box;
  margin: 0;
  vertical-align: middle;
  max-width: 100%;
  width: 100%;
  -webkit-appearance: none;
  background: transparent;
  padding: 0;
}

.uk-range:focus {
  outline: none;
}

.uk-range::-moz-focus-outer {
  border: none;
}

.uk-range::-ms-track {
  height: 15px;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

.uk-range:not(:disabled)::-webkit-slider-thumb {
  cursor: pointer;
}

.uk-range:not(:disabled)::-moz-range-thumb {
  cursor: pointer;
}

.uk-range:not(:disabled)::-ms-thumb {
  cursor: pointer;
}

.uk-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  margin-top: -7px;
  height: 15px;
  width: 15px;
  border-radius: 500px;
  background: #fff;
  border: 1px solid #ccc;
}

.uk-range::-moz-range-thumb {
  border: none;
  height: 15px;
  width: 15px;
  border-radius: 500px;
  background: #fff;
  border: 1px solid #ccc;
}

.uk-range::-ms-thumb {
  margin-top: 0;
}

.uk-range::-ms-thumb {
  border: none;
  height: 15px;
  width: 15px;
  border-radius: 500px;
  background: #fff;
  border: 1px solid #ccc;
}

.uk-range::-ms-tooltip {
  display: none;
}

.uk-range::-webkit-slider-runnable-track {
  height: 3px;
  background: #ebebeb;
  border-radius: 500px;
}

.uk-range:focus::-webkit-slider-runnable-track,
.uk-range:active::-webkit-slider-runnable-track {
  background: #d2d2d2;
}

.uk-range::-moz-range-track {
  height: 3px;
  background: #ebebeb;
  border-radius: 500px;
}

.uk-range:focus::-moz-range-track {
  background: #d2d2d2;
}

.uk-range::-ms-fill-lower,
.uk-range::-ms-fill-upper {
  height: 3px;
  background: #ebebeb;
  border-radius: 500px;
}

.uk-range:focus::-ms-fill-lower,
.uk-range:focus::-ms-fill-upper {
  background: #d2d2d2;
}

.uk-input,
.uk-select,
.uk-textarea,
.uk-radio,
.uk-checkbox {
  box-sizing: border-box;
  margin: 0;
  border-radius: 0;
  font: inherit;
}

.uk-input {
  overflow: visible;
}

.uk-select {
  text-transform: none;
}

.uk-select optgroup {
  font: inherit;
  font-weight: bold;
}

.uk-textarea {
  overflow: auto;
}

.uk-input[type='search']::-webkit-search-cancel-button,
.uk-input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}

.uk-input[type='number']::-webkit-inner-spin-button,
.uk-input[type='number']::-webkit-outer-spin-button {
  height: auto;
}

.uk-input::-moz-placeholder,
.uk-textarea::-moz-placeholder {
  opacity: 1;
}

.uk-radio:not(:disabled),
.uk-checkbox:not(:disabled) {
  cursor: pointer;
}

.uk-fieldset {
  border: none;
  margin: 0;
  padding: 0;
}

.uk-input,
.uk-textarea {
  -webkit-appearance: none;
}

.uk-input,
.uk-select,
.uk-textarea {
  max-width: 100%;
  width: 100%;
  border: 0 none;
  padding: 0 10px;
  background: #fff;
